			<img src="../../static/img/img_zjgs_btn.png" class="img_zjgs_btn" @click="toPath('/freeAccess/winningPublicity')">
		</div>
		<div class="freeAccess_goods">
			<ul class="freeAccess_type_list">
				<li v-for="(item,index) in typeList" :key="index" @click="selectType(item)">
					<span :class="nowType == item.sort?'act':''">{{ item.category }}</span>
				</li>
