</template>

<script>
	export default{
		name:'freeAccess',
		data(){
			return{
				obj:{},
				nowType:'1',
				typeList:[
					{
						id:0,
						category: "话费抵扣券",
						sort: "1",
						type: "lfl_commodity_type"
					},
					{
						id:1,
						category: "B612商品",
						sort: "2",
						type: "lfl_commodity_type"
					},
					{
						id:2,
						category: "人气商品",
						sort: "3",
						type: "populargoods"
					},
					{
						id:3,
						category: "即将开奖",
						sort: "4",
						type: "lfl_commodity_type"
					},
				],
				foodsList:[],
				rotationList:[],
				adLoadingFlag:false,
			}
		},
		mounted() {
			var jsonObj = localStorage.getItem("obj");
			this.obj = JSON.parse(jsonObj);
			this.getMediaFreeReceiveConfig();
			this.indexWinPrizeRotation();
		},
		methods:{
			goBack(){
				let backlen = window.history.length - 1;
				window.history.go(-backlen);
				this.$router.replace({ path: '/' });
			},
			toPath(path){
				let _this = this;
				_this.$router.push({path:path})
			},
			toFoodsDetails(item){
				var itemObj = JSON.stringify(item);
				localStorage.setItem("itemObj",itemObj);
				this.$router.push({path:'/freeAccess/foodsDetails'})
			},
			selectType(item){
				this.nowType = item.sort;
				this.freeGoodsList(item.type);
			},
			getMediaFreeReceiveConfig(){
				let _this = this;
				_this.adLoadingFlag = true;
				var data = _this.byte256.byte256.encode(_this.key, JSON.stringify(_this.obj));
				_this.$axios({
					method: 'post',
					url: '/freebase/getMediaFreeReceiveConfig',
					data:data,
				}).then(function(res){
					if(res.data.code === 200){
						var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
						var newArr = reData.category.sort(function(a,b){return a.sort-b.sort});
						_this.typeList = newArr;
						_this.nowType = _this.typeList[0].sort;
						_this.freeGoodsList(_this.typeList[0].type);
					}
					_this.adLoadingFlag = false;
				})
			},
			freeGoodsList(type){
				let _this = this;
				_this.adLoadingFlag = true;
				var exclusiveObj = {
					commodityType:type
				};
				var requestData = Object.assign(_this.obj,exclusiveObj);
				var redata = _this.byte256.byte256.encode(_this.key, JSON.stringify(requestData));
				_this.$axios({
					method: 'post',
					url: '/free/goods/list',
					data:redata,
				}).then(function(res){
					if(res.data.code === 200){
						var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
						for(var i in reData){
							reData[i].endTimeStr = _this.$timestampTpDate(reData[i].endTime,'ymdhms');
						}
						_this.foodsList = reData;
					}
					_this.adLoadingFlag = false;
				})
			},
			indexWinPrizeRotation(){
				let _this = this;
				_this.adLoadingFlag = true;
				var redata = _this.byte256.byte256.encode(_this.key, JSON.stringify(_this.obj));
				_this.$axios({
					method: 'post',
					url: '/prize/indexWinPrizeRotation',
					data:redata,
				}).then(function(res){
					if(res.data.code === 200){
						var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
						console.log('indexWinPrizeRotation=====',reData);
						_this.rotationList = reData;
					}
					_this.adLoadingFlag = false;
				})
			}
		}
	}
</script>

